<template>
<body id="mybodyhtml">
	<top></top>

<div>
	<div class="btn-group d-flex">
		<a href="#" @click="initt" class="btn hbtn" id="0" >A L L</a>
		<a href="#" @click="initt" class="btn hbtn" id="1" >E A T</a>
		<a href="#" @click="initt" class="btn hbtn" id="2" >B U Y</a>
		<a href="#" @click="initt" class="btn hbtn" id="3" >E N J O Y</a>
		<a href="#" class="btn hbtn" id="myaddress" data-toggle="collapse" data-target="#myinput">A D D R E S S</a>
	</div>
	<div id="myinput" class="collapse">
		<div class="input-group">
			<input id="msearch" v-model="searchloc" type="text" class="form-control" placeholder="Search for location" >
			<span class="input-group-btn">
				<button class="btn btn-secondary myinputsearch"  @click="searchlocbtn" type="button"> Go!</button>
			</span>
		</div>
	</div>
	<button id="odbtn" class="ui-btn" @click="odbtn">{{newandlike}}</button>
</div>

<div class="datas">
	<div v-for="data in datas" :key="data.d_no" class="data" style="">
		<div class="box">
			<div class="s_pic">
				<div class="ivdiv">
					<img class="mainimg" v-bind:src="'/'+data.d_path" v-bind:value="data.d_no" @click="picview" >
				</div>
			</div>
			<br>
			<div id="mynick" class="mynick">{{data.m_nick}}</div>
			<div class="mylike">
				<img src="/image/like/heart.png" class="mylikeimg" @click="checklike" value="1">{{data.d_like}}
			</div>
			<div class="myage" id="myage">
				<img class="myageimg" v-bind:src="'/image/age/'+data.m_age+'0s.png'">
			</div>
			<div>
				<p class="loctxt">{{data.d_location}}</p>
			</div>
			<div id="mytitle" class="mytitle">{{data.d_title}}</div>
		</div>
	</div>
</div>

<bottom>
</bottom>
</body>
</template>
<script>
import bottom from './bar/bottom.vue'
import top from './bar/top.vue'
export default {
	created(){
    this.$axios.get('/moment/home').then(response => {
			console.log("aaaa")
			console.log(response.data.data)
			this.datas = response.data.data;
		})
  },
  components: {
		bottom,
		top
  },
  data(){
    return {
		datas: [],
		pupps: "",
		newandlike:"N E W ▼",
		kinds:"",
		searchloc:"",
		d_no:""
    }
	},
  methods :{
	initt : function(evt){
		console.log(evt.target.id)
		this.kinds = evt.target.id
	
		var anchList = document.getElementsByClassName("btn hbtn");
	   	 for (var i = 0; i < anchList.length; i++) {
					if (i == this.kinds) {
							var anchor = document.getElementsByClassName("btn hbtn")[i];
							anchor.className ='btn hbtn active';
					}else{
						var anchor = document.getElementsByClassName("btn hbtn")[i];
							anchor.className ='btn hbtn';
					}
				 }
				 
		this.$axios.get('/moment/listinit?kind='+evt.target.id).then((response) => {
								this.datas = response.data.data
								this.newandlike="L I K E ▼"
            })
	},
	odbtn : function(evt){
		var ord ;
		if (evt.target.innerText =="N E W ▼") {
				this.newandlike="L I K E ▼"
				ord = "nw"
		} else {
				ord = "lk"
		 		this.newandlike="N E W ▼"
		}
		
		if (this.searchloc) {
			this.$axios.get('/moment/home_address_selectdb?ord='+this.ord+"&kind="+this.kinds+"&loc="+this.searchloc).then((response) => {
                console.warn(response);
                this.datas = response.data.data
    	})
		} else {
			this.$axios.get('/moment/listinit?ord='+ord+"&kind="+this.kinds).then((response) => {
									console.warn(response);
									this.datas = response.data.data
    	})
		}
	},
	searchlocbtn : function(){
			this.$axios.get('/moment/home_address_selectdb?ord='+this.ord+"&kind="+this.kinds+"&loc="+this.searchloc).then((response) => {
                console.warn(response);
                this.datas = response.data.data
    })
	},
	picview : function(evt){
			console.log(evt.target.attributes[1].value)
			location.href = "/detail?d_no="+evt.target.attributes[1].value;
	},
	checklike : function(evt){
			console.log(evt.target.attributes.src)
			console.log(evt.target.attributes)
			var heartflag = evt.target.attributes[1].value
			console.log(heartflag)
			if (heartflag == "1") {
				console.log("aaa")
				evt.target.attributes.src = '/image/like/full_heart.png'
			} else {
				
			}
	}
  }
}

</script>


<style>
@font-face {
	font-family: "Am";
	src: url("/font/AmaticSC-Regular.ttf");
}
@font-face {
	font-family: "설렘";
	src: url("/font/210 설렘주의B.ttf")
}
@font-face {
	font-family: "나눔고딕";
	src: url("/font/NANUMBARUNGOTHIC.TTF")
}

div {
	/* font-family: "�������"; */
	/* text-shadow: #999999 1px 1px 1px; */
}

div p.loctxt{
  overflow: hidden; 
  text-overflow: ellipsis;
  white-space: nowrap;
}
.headtop{
    height: 30px;
    background: black;
}
.hj_head {
   text-align: center;
   /* height: 50px; */
   background-color: black;
   color: white;
   /* font-family: "Am"; */
   /* font-size: 25pt; */
   /* vertical-align: middle; */
}
.head_moment {
   color: white;
   font-family: "Am";
   font-size: 25pt;
   vertical-align: middle;
}
#myinput{
	padding-left: 10px; 
	padding-right: 10px;
	background-color: black;	
}
.data{
	float:left; 
	margin: 30px;
	position: relative;
}
.data *{
	font-family: "설렘";
	font-size:20px;
}
.box {
	box-shadow: 5px 5px 20px #ccc;
	background-color: #e5e7e8;
	padding: 15px;
    width: 320px;
}
.mainimg {
	vertical-align: middle; 
	margin: auto; 
	height: 230px; 
	width:100%;
}
.ivdiv{
	display:table-cell; 
	vertical-align:middle; 
	height: 320px;
	background-color: black;
}
.mynick{
	float: left; 
	margin-right: 15px; 
	font-size:15px;
}
.mylike{
	float: right; 
	margin-right: 10px; 
	font-size: 15px;
}
.mylikeimg{
	width:20px;
	float:left;
	margin-right:5px;
}
.myage{
	font-size: 15px;
	margin-bottom: 10px;
}
.myageimg{
	width:50px;
	/* margin-top: 6px; */
}
.mytitle{
	margin-top: 10px; 
	text-decoration: underline; 
}
.btn.hbtn {
	border-radius: 0rem;
	background-color: black;
	font-family: "Am";
	color: white;
	font-size: 14pt;
}
.btn-group {
	width: 100%;
}
.btn.hbtn.active {
	background-color: white;
	font-family: "Am";
	color: black;
	font-size: 14pt;
}

#odbtn {
	width: 100%;
    background-color: black;
	font-size: 12pt;
	color: white;
	font-family: "Am";
	margin: auto;
	height: 40px;
	border: 0px;  
}
.btn.fbtn {
	background-color: black;
	font-family: "Am";
	color: white;
	font-size: 14pt;
}
.myinputsearch{
    background-color: white;
    color: black;
}
#msearch{
	background-color: white;
}
#bottom_nav {
	width: 100%;
	position: sticky;
	background-color: black;
}
.fbtn:active {
	background-color: #A9A9F5;
}

.fbtn:hover {
	background-color: #A9A9F5;
}

.hbtn:active {
	background-color: #A9A9F5;
}

.hbtn:hover {
	background-color: #A9A9F5;
}
</style>


